<template v-on:scroll="cons()">
  <div class="home App">
    <!-- <img alt="Vue logo" src="../assets/logo.png" /> -->
    <Navbar />
    <Landing />
    <Services />
    <Expert />
    <Creativity />
    <Steps />
    <Best />
    <Clients />
    <Footer />
  </div>
</template>

<script>
// @ is an alias to /src
import Landing from "@/components/landing/Landing.vue";
import Navbar from "@/components/navbar/Navbar.vue";
import Services from "@/components/services/Services.vue";
import Expert from "@/components/experts/Expert.vue";
import Creativity from "@/components/creative/Creative.vue";
import Steps from "@/components/steps/Steps.vue";
import Best from "@/components/best/Best.vue";
import Clients from "@/components/clients/Clients.vue";
import Footer from "@/components/footer/Footer.vue";

export default {
  name: "Home",
  components: {
    Landing,
    Navbar,
    Services,
    Expert,
    Creativity,
    Steps,
    Best,
    Clients,
    Footer,
  },

  created() {
    window.addEventListener("scroll", this.handleScroll);
  },
  unmounted() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    handleScroll() {
      if (window.scrollY >= window.innerHeight * 1.5) {
        document.querySelector("#goUp").style.display = "block";
      } else {
        document.querySelector("#goUp").style.display = "none";
      }
    },
  },
};
</script>
